html {
  height: 100%;
}

body {
  margin: 0;
  height: 100%;
}

.main > .content {
  padding: 0 10%;
}

.header {
  background: #000 url(./../img/task/header.png) 0 0/100% 100% no-repeat;
  max-height: 850px;
}

.navigation {
  display: flex;
  height: 110px;
}

.logo {
  display: flex;
  align-items: center;
  color: #fff;
  cursor: pointer;
}

.logo__close, .logo__img_small {
  display: none;
}

.nav {
  display: flex;
  margin: 0 0 0 auto;
  justify-content: center;
}

.nav__item {
  display: flex;
  align-items: center;
  font-size: 20px;
}

.nav__item + .nav__item {
  margin-left: 40px;
}

.nav__item-link {
  color: #fff;
  color-text-decoration: none;
  color-text-align: center;
}

.main-page {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 50px;
}

.main-page__article {
  max-width: 60%;
  color: #fff;
}

.main-page__mobile {
  margin-bottom: 50px;
}

.main-page__title {
  font-size: 38px;
}

.main-page__text {
  font-size: 20px;
}

.main-page__button {
  margin-top: 50px;
  border: 1px solid #fff;
  border-radius: 3px;
  background: transparent;
  text-transform: uppercase;
  color: #fff;
  padding: 15px 50px;
  font-size: 15px;
  cursor: pointer;
  outline: none;
}

.advantages {
  background-color: #fff;
}

.advantages__article {
  padding: 100px 0;
}

.advantages__article-one {
  font-size: 18px;
  text-align: center;
  color: #ff8400;
  text-transform: uppercase;
}

.advantages__article-two {
  font-size: 36px;
  text-align: center;
}

.advantages__description {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding-bottom: 100px;
  justify-content: space-between;
  margin: 0;
}

.advantages__description-content {
  width: 25%;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 0 4px;
}

.bot {
  background: #000 url(./../img/task/bg2.png) 0 0/100% 100% no-repeat;
  max-height: 567px;
  text-align: center;
  padding-top: 100px;
}

.bot__container {
  width: 100%;
  padding-top: 35px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.bot__container-title {
  font-size: 36px;
  margin: 0 0 35px 0;
  text-align: center;
  color: #fff;
}

.bot__container-article {
  color: #fff;
  max-width: 60%;
  margin-bottom: 50px;
}

.bot__container-form {
  margin-bottom: 100px;
  width: 100%;
}

.bot__container-input {
  width: 40%;
  box-sizing: border-box;
  border: 1px solid #fff;
  border-radius: 3px;
  background: transparent;
  padding: 15px 50px;
  font-size: 15px;
  outline: none;
  color: #fff;
}

.bot__container-input::placeholder {
  color: #fff;
}

.bot__container-button {
  height: 100%;
  margin-left: 30px;
  border: 1px solid #fff;
  border-radius: 3px;
  background: #fff;
  text-transform: uppercase;
  color: #ff8400;
  padding: 15px 50px;
  font-size: 15px;
  cursor: pointer;
  outline: none;
}

.footer {
  height: 110px;
  background-color: #fff;
  display: flex;
  align-items: center;
}

.footer__buttons {
  border: 0;
  background: none;
  cursor: pointer;
  text-decoration: none;
  color: #808080;
}

.footer__buttons + .footer__buttons {
  margin-left: 5px;
}

.footer__copyright {
  margin: 0;
  padding: 50px 0;
  font-size: 10px;
}

.footer__social-icons {
  margin-left: auto;
}

.advantages__description-text, .advantages__description-title, .bot__container-article {
  font-size: 16px;
}

@media (min-width: 320px) and (max-width: 1023px) {
  .header {
    max-height: none;
  }
  .bot {
    max-height: none;
  }
  .bot__article {
    padding: 0;
  }
  .bot__input {
    width: auto;
  }
  .bot__form {
    display: flex;
    width: auto;
  }
  .advantages__description-content {
    width: 100%;
  }
  .main-page {
    flex-direction: column;
  }
  .main-page__article {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 35px;
  }
  .main > .content {
    padding: 0 5%;
  }
}

@media screen and (min-width: 320px) and (max-width: 749px) {
  .nav, .logo__img {
    display: none;
  }
  .logo__img_small {
    display: block;
    width: 50px;
  }
  .bot__container-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .bot__container-input {
    width: auto;
  }
  .bot__container-button {
    margin: 30px 0 0;
  }
  #logo:checked + .nav {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.9);
    z-index: 1;
    padding-left: 5%;
  }
  #logo:checked + .nav .nav__item {
    margin-left: 0;
  }
  .logo__close {
    display: block;
    color: #fff;
    cursor: pointer;
    font-size: 20px;
    margin-bottom: 20px;
  }
}
